
import React, { useState, useEffect, useReducer } from 'react';
import axios from 'axios';
import bridge from '@vkontakte/vk-bridge';
import {
	Button, Panel, PanelHeader, Cell, Div, Avatar, FixedLayout,
	Tabs, TabsItem, Link, Spinner, ActionSheet, ActionSheetItem,

} from '@vkontakte/vkui';
import Icon20Info from '@vkontakte/icons/dist/20/info';
import Icon16Fire from '@vkontakte/icons/dist/16/fire';
import Icon16CheckCircle from '@vkontakte/icons/dist/16/check_circle';

import styles from "../styles/custom-styles.sass";
import { Shop, Inventory } from '../components';
import requestMethod from '../helpers/requestMethod';
import { getRankByRating } from '../helpers';
import Logger from '../helpers/logger';

// Loggers
const vk = new Logger('VK');

const Home = ({ id, go, fetchedUser, tabShop, tabHome, tabTop, activeTab,
	dataUsers, progressOnChange, dispatch, state, incDec,
	dataShop, inventoryOpen, inventory, dataInventory,
	getUser, durkaCoins, shisoids, transfer, targetId, onChangeHandlerId,
	onChangeHandlerAmount, amount, activeModal, openTransfer, snackbar,
	userPlace, rating, scheme, openInfo, buyItem, openBuyDurkaCoins, openBuyItem,
	$useItem, targetIdInventory, openUseItem, ranks, goTop, scrollUp,
	getOperationsHistory, networkStatus, updateConnection,
	socketReconnection, socketReconnect, connectTwoDevices, stub, online,
	setPopout
}) => {


	useEffect(() => {
		setTimeout(() => {
			addGroup();
		}, 5000);
	}, []);



	function openShareLink() {
		const link = `https://vk.com/app7351258#${fetchedUser.id}`;
		bridge.send("VKWebAppShare", {
			"link": link
		});
	}

	function story() {
		const url = `https://vk.com/app7351258#${fetchedUser.id}`;

		bridge.sendPromise('VKWebAppShowStoryBox', {
			background_type: 'image',
			url: 'https://sun9-28.userapi.com/c855732/v855732341/215725/RVJstbppzyk.jpg',
			attachment: {
				text: 'game',
				type: 'url',
				url: url
			}
		}).then(res => {
			if (res.data.result === true) {
				axios.post(`${requestMethod}/story`, {
					search: document.location.search,
				});
			}
		}).catch(error => {
			vk.log('story error:', error);
		});
	}

	function AddToCommunity() {
		bridge.send("VKWebAppAddToCommunity", {});
	}

	function addGroup() {
		bridge.send('VKWebAppJoinGroup', { 'group_id': 192779261 });
	}

	function share(e) {
		//  ДОБАВЛЕНИЕ РЕПОСТА НА СТЕНУ ПОЛЬЗОВАТЕЛЯ
		e.preventDefault();
		const url = `https://vk.com/app7351258#${fetchedUser.id}`;

		bridge.send('VKWebAppShowWallPostBox', {
			message: `А ты уже был в ДУРКЕ? Приложение - ${url}`,
			attachments: `photo73606509_457262018,https://vk.com/app7351258#${fetchedUser.id}`,
		});
	}

	// Копирование в буфер
	function copyLink() {
		const link = `https://vk.com/app7351258#${fetchedUser.id}`;
		bridge.sendPromise('VKWebAppGetClientVersion').then((result) => {
			vk.log(result.platform);
			if (result.platform === 'web' || result.platform === 'mobile-web') {
				window.navigator.clipboard.writeText(link).
					then(() => {
						vk.log('successfully set', link);
					}, () => {
						vk.log('write failed', link);
					});
			} else {
				bridge.send('VKWebAppCopyText', { text: link });
			}
		}).catch(error => {
			vk.log(error);
		});
	}



	function openBase() {
		setPopout(<>
			<ActionSheet onClose={() => setPopout(null)}>
				<ActionSheetItem autoclose onClick={openShareLink} >
					Поделиться ссылкой
				</ActionSheetItem>
				<ActionSheetItem autoclose onClick={copyLink} >
					Скопировать ссылку
				</ActionSheetItem>
				<ActionSheetItem autoclose onClick={story}>
					Поделиться в истории
				</ActionSheetItem>
				<ActionSheetItem autoclose onClick={share}>
					Поделиться на стене
				</ActionSheetItem>
			</ActionSheet>
		</>);
	}



	const renderShop = dataShop.map((item, index) => {
		return (
			<Shop
				key={index}
				id={item.id}
				name={item.name}
				price={item.price}
				icon={item.icon}
				openInfo={openInfo}
				transfer={transfer}
				buyItem={buyItem}
				openBuyItem={openBuyItem}
				scheme={scheme}

			/>
		);
	});

	const renderInventory = dataInventory.map((item, index) => {
		return (
			<Inventory
				key={index}
				id={item.id}
				name={item.name}
				price={item.price}
				icon={item.icon}
				count={item.count}
				openInfo={openInfo}
				transfer={transfer}
				buyItem={buyItem}
				openBuyItem={openBuyItem}
				$useItem={$useItem}
				targetIdInventory={targetIdInventory}
				openUseItem={openUseItem}
				scheme={scheme}
			/>
		);
	});

	return (
		<Panel id={id}>
			<PanelHeader>ДУРКА</PanelHeader>

			{connectTwoDevices
				?
				stub('Вы подключились с 2 устройств ', socketReconnect, "Переподключиться")
				:
				<>
					<FixedLayout vertical="top" filled={true}>
						<Tabs >
							<TabsItem
								onClick={goTop} data-to="top"
							// selected={activeTab === 'top'}
							>
								ТОП
                    </TabsItem>
							<TabsItem
								onClick={tabHome}
								selected={activeTab === 'home'}
							>
								Профиль
                    </TabsItem>
							<TabsItem
								onClick={tabShop}
								selected={activeTab === 'shop'}
							>
								Магазин
                    </TabsItem>
						</Tabs>
					</FixedLayout>

					{networkStatus
						?
						stub('Отсутствует интернет - соединение!', updateConnection, "Повторить")
						:
						<>
							{socketReconnection
								?
								stub('Ошибка подключения к серверу', socketReconnect, "Повторить")
								:
								<>
									{
										!rating
											?
											<div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
												<Spinner size="large" style={{ marginTop: 90 }} />
											</div>
											:
											<>
												{activeTab === 'home'
													?
													<>
														<Div className="main">
															{/* <Div className="flexStart"> */}
															{fetchedUser &&
																<Div className="div" style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>

																	<Avatar src={fetchedUser.photo_200} size={100} />

																	<Div className="div" style={{ marginTop: 5 }}>
																		{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
																	</Div>
																</Div>
															}
														</Div>

														<Div className="mainLeft">
															<Div className="divPaddingLeft">

																<Div className="divFlexInfo">
																	<Icon16CheckCircle fill="green" className="iconInfo" />
																	<Div className="divInfoRating">
																		Онлайн: {online}
																	</Div>
																</Div>

																<Div className="divFlexInfo">
																	<Icon16Fire fill="red" className="iconInfo" />
																	<Div className="divInfoRating">
																		Рейтинг: {rating}
																	</Div>
																</Div>

																<Div className="divFlexInfo">
																	<Icon20Info onClick={go} data-to="rank-info" className="iconInfo" fill="#0000FF" />
																	<Div className="divInfoRank">
																		Звание: {getRankByRating(rating, ranks)}
																		<Link className="linkInfo" onClick={go} data-to="rank-info">
																			Подробнее
                                                                        </Link>
																	</Div>
																</Div>

															</Div>
															<Div className="divPaddingLeft">
																Привлечено шизоидов: {shisoids}
															</Div>
															<Div className="divPaddingLeft">
																DurkaCoins: {durkaCoins}
															</Div>
														</Div>



														<Div className="divBtn">
															<Button
																mode="commerce"
																size="xl"
																stretched
																onClick={openBase}
																className={scheme === "bright_light" ? "btnBlack" : "btnGray"}
															>
																Привлечь шизоидов
                                    </Button>


															<Div className="flexBtn">
																<Button
																	className={scheme === "bright_light" ? "buttonPaymentBlackLeft" : "buttonPaymentGrayLeft"}
																	mode="commerce"
																	onClick={addGroup}
																	size="xl"
																	stretched
																>
																	Подписаться
                            </Button>
																<Button
																	className={scheme === "bright_light" ? "buttonPaymentBlackRight" : "buttonPaymentGrayRight"}
																	mode="commerce"
																	onClick={go}
																	data-to="how-it-works"
																	size="xl"
																	stretched
																>
																	Правила
                            </Button>

															</Div>


															<Div className="divFlex">
																DurkaCoins
						</Div>

															<Div className="flexBtn">

																<Button
																	className={scheme === "bright_light" ? "buttonPaymentBlackLeft" : "buttonPaymentGrayLeft"}
																	mode="commerce"
																	onClick={openTransfer}
																	size="xl"
																	stretched
																>
																	Перевести
                                        </Button>


																<Button
																	className={scheme === "bright_light" ? "buttonPaymentBlackRight" : "buttonPaymentGrayRight"}
																	mode="commerce"
																	onClick={openBuyDurkaCoins}
																	size="xl"
																	stretched
																>
																	Купить
                                        </Button>

															</Div>

															<Button
																mode="commerce"
																size="xl"
																stretched
																onClick={go} data-to="operations-history"
																className={scheme === "bright_light" ? "btnBlackHistoty" : "btnGrayHistoty"}
															>
																История операций
                                    </Button>

															<Button
																mode="commerce"
																size="xl"
																stretched
																onClick={AddToCommunity}
																className={scheme === "bright_light" ? "btnBlackHistoty" : "btnGrayHistoty"}
															>
																Добавить в группу
                                    </Button>

															<Div className="mainInventory">
																{inventory
																	?
																	<Link
																		className="inventory"
																		id={id}
																		onClick={inventoryOpen}
																		mode="tertiary"
																	>
																		Открыть инвентарь
                                            </Link>
																	:
																	<>
																		<Link
																			className="inventory"
																			id={id}
																			onClick={inventoryOpen}
																			mode="tertiary"
																		>
																			Закрыть инвентарь
					                            </Link>

																		{renderInventory
																			?

																			<>
																				{renderInventory}
																				<Div>
																					<Link
																						id={id}
																						onClick={scrollUp}
																						mode="tertiary"
																					>
																						Вверх
					                                    </Link>
																				</Div>
																			</>
																			:
																			<Div>
																				У Вас еще нету товаров из <> </>
																				<Link
																					id={id}
																					onClick={tabShop}
																					mode="tertiary"
																				>
																					Магазина.
					                                    </Link>
																			</Div>
																		}





																	</>
																}
															</Div>



														</Div>





													</>
													: ''
												}



												{activeTab === 'shop'
													?
													<Div className="shop">
														<Div className="divShop">
															Баланс DurkaCoins: {durkaCoins}
														</Div>
														{renderShop}
													</Div>
													:
													''
												}


												{snackbar}
											</>
									}
								</>
							}


						</>
					}
				</>

			}







		</Panel>
	);
};

export { Home };
